<template>
  <div class="carousel-container">

    <div class="carousel-slide" v-for="(image, index) in urlsWithHttp" :key="index" :class="{ active: index === currentIndex }">
      <img :src="image" alt="Slide {{ index + 1 }}" style="width: 100%;">
       <!-- <p v-for="(url, index) in urlsWithHttp" :key="index" class="url-item">{{ url }}</p>  -->
    </div>
  </div>
</template>

<script>
export default {
  name: 'arrayCarousel',
  props: {
    projectPath: {
      type: String,
      required: true
    },
    urls: {
      type: Array,
      required: true
    }
  },
  data() {
 
    return {
      currentIndex: 0,
      intervalId: null
    };
  },
  mounted() {
     this.startAutoPlay();
  
  },
  computed: {
    urlsWithHttp() {
      // Add "https://res.cloudinary.com/alvin-tan/image/upload/v1717129154/" prefix to each URL
      return this.urls.map(url => "https://res.cloudinary.com/alvin-tan/image/upload/" +   this.projectPath + "/" +url);
    }
  },
  methods: {
    startAutoPlay() {
      this.intervalId = setInterval(this.nextSlide, 3000); // Change image every 3 seconds
    },
    stopAutoPlay() {
      clearInterval(this.intervalId);
    },
    prevSlide() {
      this.stopAutoPlay();
      this.currentIndex = (this.currentIndex - 1 + this.urls.length) % this.images.length;
      this.startAutoPlay();
    },
    nextSlide() {
      this.stopAutoPlay();
      this.currentIndex = (this.currentIndex + 1) % this.urls.length;
      this.startAutoPlay();
    }
  }
};
</script>

<style scoped>
.carousel-container {
  overflow: hidden;
  position: relative;
  width: 60%;  /* Set the container to 80% width */
  margin: 0 auto;  /* Center align the container */
  height: auto;
}

.carousel-slide {
  display: none;
  width: 100%;  /* Ensure each slide takes the full width of the container */
  height: auto;
}

.carousel-slide.active {
  display: block;
}
</style>
