<template>
  <div>
    <div class="url-list">
      <div v-if="urls.length === 0">No URLs found in the file.</div>
      <div v-else>
        <div class="image-scroll-container">
          <div class="image-container">
            <img
              v-for="(url, index) in urlsWithHttp"
              :key="index"
              :src="url"
              alt="image"
              class="image-item"
              @click="enlargeImage(url)"
            />
          </div>
        </div>
      </div>
    </div>
    <div v-if="isModalVisible" class="modal" @click="closeModal">
      <span class="close">&times;</span>
      <img class="modal-content" :src="selectedImage">
    </div>
  </div>
</template>

<script>
export default {
  name: 'arrayImage',
  props: {
    projectPath: {
      type: String,
      required: true
    },
    urls: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      isModalVisible: false,
      selectedImage: ''
    };
  },
  computed: {
    urlsWithHttp() {
      return this.urls.map(url => "https://res.cloudinary.com/alvin-tan/image/upload/" + this.projectPath + "/" + url);
    }
  },
  methods: {
    enlargeImage(url) {
      this.selectedImage = url;
      this.isModalVisible = true;
    },
    closeModal() {
      this.isModalVisible = false;
      this.selectedImage = '';
    }
  }
};
</script>

<style>
.url-list {
  margin-top: 20px;
}

.image-scroll-container {
  overflow-x: auto;
  white-space: nowrap;
}

.image-container {
  display: inline-block;
}

.image-item {
  width: auto;
  max-width: 100%;
  height: 300px; /* Set a fixed height for the images */
  margin: 5px;
  object-fit: contain;
  cursor: pointer; /* Add cursor pointer to indicate clickable items */
}

.modal {
  display: block; /* Hidden by default */
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  padding-top: 100px; /* Location of the box */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0,0.9); /* Black w/ opacity */
}

.close {
  position: absolute;
  top: 15px;
  right: 35px;
  color: #f1f1f1;
  font-size: 40px;
  font-weight: bold;
  transition: 0.3s;
}

.close:hover,
.close:focus {
  color: #bbb;
  text-decoration: none;
  cursor: pointer;
}

.modal-content {
  margin: auto;
  display: block;
  width: 80%;
  max-width: 700px;
}

@media only screen and (max-width: 700px) {
  .modal-content {
    width: 100%;
  }
}
</style>
